*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

*:focus {
  outline: none;
}

html {
  font-size: 62.5%;
}

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: #272727;
  background-color: #ecf0f1;
  min-height: 100vh;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#396afc), to(#2948ff));
  background-image: -webkit-linear-gradient(left top, #396afc, #2948ff);
  background-image: -o-linear-gradient(left top, #396afc, #2948ff);
  background-image: linear-gradient(to right bottom, #396afc, #2948ff);
  background-size: cover;
  background-position: center;
}



